import { useState, useEffect } from "react";

const mobileMaxWidth = 414;

const useWindowSize = () => {
  const isClient = typeof window === "object";

  function getSize() {
    const desktop = isClient ? window.innerWidth > mobileMaxWidth : true;
    const mobile = !desktop;

    return {
      width: isClient ? window.innerWidth : 300, // 300 min
      height: isClient ? window.innerHeight : 800, // 800 min
      desktop,
      mobile,
    };
  }

  const [windowSize, setWindowSize] = useState(getSize);

  useEffect(() => {
    function handleResize() {
      setWindowSize(getSize());
    }

    const fetchData = async () => {
      if (!isClient) {
        return false;
      }

      window.addEventListener("resize", handleResize);
    };
    fetchData();

    return () => window.removeEventListener("resize", handleResize); // on unmount
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Empty array ensures that effect is only run on mount and unmount

  return windowSize;
};

export default useWindowSize;
