import React from "react";
import { useWindowSize } from "hooks";
import { imagePath } from "services/constants";
import { MovieClass } from "core/MovieClass";
import { ButtonBar } from "components";
import s from "./Movie.module.css";

type Props = {
  movie: MovieClass;
};

// ----------
// COMPONENT:
const Movie = ({ movie }: Props) => {
  // CUSTOM HOOKS:
  const { desktop } = useWindowSize();

  // CONSTANTS:
  const imageUrl = movie.posterPath ? imagePath + movie.posterPath : undefined;

  // RENDER:
  return (
    <div className={s.root}>
      <img className={s.poster} alt="" src={imageUrl} />

      <div>{movie.title}</div>

      <div className={desktop ? s.buttonBar : s.buttonBarMobile}>
        <ButtonBar movie={movie} />
      </div>
    </div>
  );
};

export default Movie;
