import React from "react";
import { DisplayType } from "core/types";
import s from "./DisplaySelector.module.css";

type Props = {
  display: DisplayType;
  onDisplayClick: (input: DisplayType) => void;
};

// ----------
// COMPONENT:
const DisplaySelector = ({ display, onDisplayClick }: Props) => {
  return (
    <div className={s.root}>
      <div className="appButtons">
        <button type="button">
          <img
            src={display === "search" ? "./search.png" : "./search-gray.png"}
            alt=""
            onClick={() => onDisplayClick("search")}
          />
        </button>

        <button type="button">
          <img
            src={display === "favourites" ? "./star.svg" : "./star-gray.svg"}
            alt=""
            onClick={() => onDisplayClick("favourites")}
          />
        </button>

        <button type="button">
          <img
            src={display === "watchLater" ? "./clock.svg" : "./clock-gray.svg"}
            alt=""
            onClick={() => onDisplayClick("watchLater")}
          />
        </button>
      </div>
    </div>
  );
};

export default DisplaySelector;
