import React, { useState } from "react";
import { AppContext, DefaultContext } from "core/appContext";
import { useWindowSize } from "hooks";
import { DisplayType } from "core/types";
import { DisplaySelector, Moovy } from "components";
import "./App.css";

const displayTypeDefault: DisplayType = "search";

function App() {
  // CUSTOM HOOKS:
  const { desktop } = useWindowSize();

  // STATE:
  const [display, setDisplay] = useState(displayTypeDefault);

  // FUNCTIONS:
  const onDisplayClick = (input: DisplayType) => {
    setDisplay(input);
  };

  // RENDER:
  return (
    <AppContext.Provider value={DefaultContext}>
      <div className="app">
        <header className="flexRowStretch">
          <h1>Moovy</h1>

          <DisplaySelector display={display} onDisplayClick={onDisplayClick} />

          {desktop && <img src="./moo-fo.png" alt="moo-fo" className="mooFo" />}
        </header>

        <Moovy display={display} />
      </div>
    </AppContext.Provider>
  );
}

export default App;
