import React, { useContext, useState } from "react";
import { AppContext, AppContextType } from "core/appContext";
import { useWindowSize } from "hooks";
import { DisplayType } from "core/types";
import { MovieClass } from "core/MovieClass";
import { movieSearch } from "services/movieSearch";
import { Movie } from "components";
import s from "./Moovy.module.css";

const moviesDefault: MovieClass[] = [];

type Props = {
  display: DisplayType;
};

// ----------
// COMPONENT:
const Moovy = ({ display }: Props) => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);
  const { moviesFavourite, moviesWatchLater } = context;

  // CUSTOM HOOKS:
  const { desktop, mobile } = useWindowSize();

  // STATE:
  // const [apiKey, setApiKey] = useState("d4628d3b003cc1fc6a008c7c53bdae46");
  const apiKey = "d4628d3b003cc1fc6a008c7c53bdae46";
  const [searchText, setSearchText] = useState("Pippi");
  const [movies, setMovies] = useState(moviesDefault);
  const [loading, setLoading] = useState(false);

  // FUNCTIONS:
  // const onApiKeyChange = (event: any) => {
  //   setApiKey(event.target.value);
  // };

  const onSearchTextChange = (event: any) => {
    setSearchText(event.target.value);
  };

  const onSearchClick = async () => {
    if (!apiKey) {
      return;
    }

    setLoading(true);

    setMovies(await movieSearch(apiKey, searchText));
    setLoading(false);
  };

  // CONSTANTS:
  let moviesToDisplay = [];
  if (display === "favourites") {
    moviesToDisplay = moviesFavourite;
  } else if (display === "watchLater") {
    moviesToDisplay = moviesWatchLater;
  } else {
    moviesToDisplay = movies;
  }

  // RENDER:
  return (
    <div className={s.root}>
      {display === "favourites" && <h2>Favourites</h2>}
      {display === "watchLater" && <h2>Watch later</h2>}

      {display === "search" && (
        <>
          {/* <div className="flexRow">
            <div className={"label " + s.label}>Your api key:</div>
            <input
              type="text"
              className="textBox"
              value={apiKey}
              onChange={onApiKeyChange}
            />
          </div> */}

          <div className="flexRow">
            <div className={"label " + s.label}>Text:</div>
            <input
              type="text"
              className="textBox"
              value={searchText}
              onChange={onSearchTextChange}
            />
            {desktop && <button onClick={onSearchClick}>Search</button>}
          </div>

          {mobile && (
            <div className="flexRowRight">
              <button onClick={onSearchClick}>Search</button>
            </div>
          )}
        </>
      )}

      {loading && <div>Mooooooooo...</div>}

      {moviesToDisplay && moviesToDisplay.length > 0 && (
        <div className={s.movies}>
          {moviesToDisplay.map(movie => (
            <Movie key={movie.id} movie={movie} />
          ))}
        </div>
      )}
    </div>
  );
};

export default Moovy;
