import React, { useContext, useState } from "react";
import { AppContext, AppContextType } from "core/appContext";
import { MovieClass } from "core/MovieClass";
import s from "./ButtonBar.module.css";

type Props = {
  movie: MovieClass;
};

// ----------
// COMPONENT:
const ButtonBar = ({ movie }: Props) => {
  // CONTEXT:
  const context: AppContextType = useContext(AppContext);

  // FUNCTIONS:
  const getIsFavourite = () =>
    context.moviesFavourite.some((x: MovieClass) => x.id === movie.id);
  const getIsWatchLater = () =>
    context.moviesWatchLater.some((x: MovieClass) => x.id === movie.id);

  // STATE:
  const [isFavourite, setIsFavourite] = useState(getIsFavourite());
  const [isWatchLater, setIsWatchLater] = useState(getIsWatchLater());

  const onFavouriteClick = () => {
    context.moviesFavourite = isFavourite
      ? context.moviesFavourite.filter((x) => x.id !== movie.id)
      : [...context.moviesFavourite, movie];

    setIsFavourite(!isFavourite);
  };
  const onWatchLaterClick = () => {
    context.moviesWatchLater = isWatchLater
      ? context.moviesWatchLater.filter((x) => x.id !== movie.id)
      : [...context.moviesWatchLater, movie];

    setIsWatchLater(!isWatchLater);
  };

  // RENDER:
  return (
    <div className={s.root}>
      <img
        src={isFavourite ? "./star.svg" : "./star-gray.svg"}
        alt=""
        onClick={onFavouriteClick}
      />

      <img
        src={isWatchLater ? "./clock.svg" : "./clock-gray.svg"}
        alt=""
        onClick={onWatchLaterClick}
      />
    </div>
  );
};

export default ButtonBar;
